.Main-Menu {
  top: 20px;
  margin-top: -150px;
  display: flex;
  flex-direction: row;
  z-index: 100;
  width: fit-content;
  padding: 14px 18px;
  align-items: flex-start;
  border-radius: 70px;
  background: rgba(255, 255, 255, 0.8);
  box-sizing: border-box;
}

@keyframes moveLeftToRight {
  0% { transform: translateX(0); }
  50% { transform: translateX(-50%); }
  100% { transform: translateX(-50%); }
}

@keyframes moveRightToLeft {
  0% { transform: translateX(-50%); }
  50% { transform: translateX(0); }
  100% { transform: translateX(0); }
}


.Carousal {
  width: 100%;
  display: flex;
  flex-direction: column;
  margin-bottom: 300px;
  box-sizing: border-box;
  overflow-x: hidden;
}



.Carousal-row {
  width: fit-content;
  display: flex;
  flex-direction: row;
  box-sizing: border-box;
  will-change: transform;

}

.Carousal-item {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  align-content: center;
  width: 400px;
  height: 300px;
  border-radius: 24px;
  background: #114F3D;
  margin: 42px;
  box-sizing: border-box;
}

.see-more-image {
  position: absolute;
  display: none;
  opacity: 0;
  transition: opacity 0.3s ease;
}

.Carousal-item:hover .see-more-image {
  display: block;
  opacity: 1;
}

.Carousal-item:hover {
  cursor: pointer;
}



.Carousal .Carousal-row:first-child {
  animation: moveLeftToRight 10s linear infinite;
}

.Carousal .Carousal-row:last-child {
  animation: moveRightToLeft 10s linear infinite;
}

.Carousal .Carousal-row {
  animation-play-state: running; /* Ensure animation is running by default */
}

.Carousal .Carousal-row:hover {
  animation-play-state: paused; /* Pause animation on hover */
}



